.weatherInfo {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: 15px;
	border: 1px solid white;
	flex-basis: 46%;
}

.leftZone {
	flex-basis: 35%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	gap: 30px;
}

.info {
	color: #b7b5c0;
}

.title {
	font-size: 30px;
}
